import {
	mapMutations
} from 'vuex';

export default {
	data() {
		return {
			mobile: '',
			password: '',
			logining: false,
			user: {},
			pricePencent:''
		}
	},
	onLoad() {
		uni.request({
			url: this.api + '/api/queryPricePercent',
			success: (res) => {
				console.log(res.data)
				this.pricePencent  = 1 + res.data.pricePencent / 100
			}
		});
	},
	methods: {
		...mapMutations(['login']),
		inputChange(e) {
			const key = e.currentTarget.dataset.key;
			this[key] = e.detail.value;
		},
		navBack() {
			uni.navigateBack();
		},
		toRegist() {
			this.$api.msg('去注册');
			uni.navigateTo({
				url: 'Register/Register'
			});
		},
		toResetPassword() {
			this.$api.msg('找回密码');
			uni.navigateTo({
				url: 'ResetPassword/ResetPassword'
			});
		},
		async toLogin() {
			uni.request({
				url: this.api + '/api/login',
				data: {
					userName: this.mobile,
					password: this.password
				},
				success: (res) => {
					this.$api.msg(res.data.msg);
					if (res.data.code == 1) {
						this.$api.msg(res.data.msg);
						this.logining = true;
						// var api ="http://192.168.1.11:8080"
						uni.request({
							url: this.api + '/api/loadUserInfo',
							data: {
								token: res.data.token
							},
							success: (res1) => {
								this.user = res1.data.member
								// console.log(this.user)
								var user = {
									token: res.data.token,
									id: this.user.hybh00,
									mobile: this.user.shouji,
									nickname: this.user.hyming,
									portrait: this.api + this.user.txiang,
									jifen0: this.user.jifen0 + "",
									yongji: this.user.yongji,
									hydji0: this.user.hydji0,
									pricePencent:this.pricePencent
								}
								this.login(user);
								uni.navigateBack({
									url: '../user/user'
								});
							}
						});

					} else {
						this.$api.msg(res.data.msg);
						this.logining = false;
					}
				}
			});
		}
	},

}
